export default class VeiculoService {
    constructor(resource) {
        this._resource = resource('veiculo{/id}');
    }

    get(id) {
        return this._resource
            .get({ id })
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel obter o registro');
            })
    }

    save(veiculo) {
        return this._resource
            .save(veiculo)
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel salvar o registro');
            })
    }

    update(id, veiculo) {
        return this._resource
            .update({ id }, veiculo)
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel salvar o registro');
            })
    }

    delete(id) {
        return this._resource
            .delete({ id })
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel remover o registro');
            })
    }
}



