export default class PlanoManutencaoService {
    constructor(resource) {
        this._resource = resource('planoManutencao{/id}');
    }

    get(id) {
        return this._resource
            .get({ id })
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel obter o registro');
            })
    }

    save(planoManutencao) {
        return this._resource
            .save(planoManutencao)
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel salvar o registro');
            })
    }

    update(id, planoManutencao) {
        return this._resource
            .update({ id }, planoManutencao)
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel salvar o registro');
            })
    }

    delete(id) {
        return this._resource
            .delete({ id })
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel remover o registro');
            })
    }
}



