export default class LoginService {
    constructor(resource) {
        this._resource = resource;
    }

    login(email, senha) {
        return this._resource
            .post('auth', { email, senha })
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel realizar o login');
            });
    }

    getUserInfo(email) {
        return this._resource
            .get(`auth/userInfo/${email}`)
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel recuperar as informaes do usurio');
            });
    }

    getUserMenus(perfil) {
        return this._resource
            .get(`auth/userMenus/${perfil}`)
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel recuperar os menus do usurio');
            });
    }
}



