export default class TelaPerfilService {
    constructor(resource) {
        this._resource = resource('telaPerfil{/id}');
    }

    get(id) {
        return this._resource
            .get({ id })
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel obter o registro');
            })
    }

    save(telaPerfil) {
        return this._resource
            .save(telaPerfil)
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel salvar o registro');
            })
    }

    update(id, telaPerfil) {
        return this._resource
            .update({ id }, telaPerfil)
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel salvar o registro');
            })
    }

    delete(id) {
        return this._resource
            .delete({ id })
            .then(res => res.json(), err => {
                console.log(err);
                throw new Error('No foi possvel remover o registro');
            })
    }
}



